.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  padding-left: 86px;
}
.header__wrapp {
  .container {
    padding: 0;
  }
}
.header-top {
  padding: 30px 15px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 165px;
    img {
      width: 100%;
    }
  }
  &__connection {
    margin-left: 125px;
  }
  &__tel {
    display: flex;
    align-items: center;
    &:hover {
      span {
        color: #c9a028;
      }
    }
    img {
      width: 16px;
    }
    span {
      font-size: 15px;
      line-height: 105%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffba01;
      margin-left: 6px;
    }
  }
  &__mail {
    font-size: 15px;
    line-height: 105%;
    color: #ffba01;
    padding-left: 22px;
    margin-top: 8px;
    display: block;
    &:hover {
      color: #c9a028;
    }
  }
  &__location {
    display: flex;
    &-img {
      width: 12px;
    }
    &-img img {
      width: 100%;
    }
    &-desc {
      margin-left: 6px;
      span:nth-child(1) {
        font-size: 15px;
        line-height: 105%;
        font-weight: 700;
        color: #ffffff;
        display: block;
      }
      span:nth-child(2) {
        font-size: 15px;
        line-height: 105%;
        color: #ffffff;
        margin-top: 8px;
        display: block;
      }
    }
  }
  &__btn {
    width: 168px;
    height: 46px;
    margin-left: 125px;
  }
}
.header-bottom {
  padding: 0 15px;
  &__list {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    background-color: #ffffff;
    li {
      height: 100%;
    }
  }
  &__link2 a {
    &:after {
      display: none;
    }
  }
  &__link a {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 26px;
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #081529;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -6px;
      transform: translate(0, -50%);
      width: 4px;
      height: 20px;
      background-color: #ffba01;
    }
    &:hover {
      background-color: #1a2a43;
      color: #ffba01;
      &:after {
        z-index: -1;
      }
    }
  }
  &__item {
    position: relative;
    &--active {
      .header-bottom__show {
        background-color: #1a2a43;
        span {
          color: #ffba01;
        }
        svg path {
          fill: #ffba01;
        }
        &:after {
          z-index: -1;
        }
      }
      .header-bottom__hidden {
        display: block;
      }
    }
  }
  &__show {
    height: 100%;
    padding: 0 26px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all 0.3s;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      right: -6px;
      transform: translate(0, -50%);
      width: 4px;
      height: 20px;
      background-color: #ffba01;
    }
    &:hover {
      background-color: #1a2a43;
      &:after {
        z-index: -1;
      }
      span {
        color: #ffba01;
      }
      svg path {
        fill: #ffba01;
      }
    }
    svg {
      flex-shrink: 0;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #081529;
      margin-right: 6px;
    }
  }
  &__hidden {
    position: absolute;
    z-index: 3;
    top: 100%;
    left: 0;
    background-color: #040d1b;
    padding: 30px 20px 30px 20px;
    width: 100%;
    min-width: 265px;
    display: none;
    transition: all 0.3s;
  }
  &__hidden-list a {
    display: block;
    font-size: 14px;
    line-height: 120%;
    color: #ffffff;
    padding-left: 17px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 5px;
      height: 5px;
      background-color: #ffba01;
    }
    &:not(:first-child) {
      margin-top: 15px;
    }
    &:hover {
      color: #ffba01;
    }
  }
}
.header-mob {
  padding: 25px 0 0 0;
  display: none;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 130px;
    img {
      width: 100%;
    }
  }
  &__burger {
    width: 25px;
    height: 25px;
    position: relative;
    z-index: 10;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 11px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 12px;
        transform: rotate(45deg);
      }
    }
  }
}

@media screen and (max-width: 1380px) {
  .header-bottom {
    &__list {
      justify-content: start;
    }
    &__link a {
      font-size: 13px;
      padding: 0 10px;
      &:after {
        right: -2px;
        width: 2px;
      }
    }
    &__show {
      padding: 0 10px;
      &:after {
        right: -2px;
        width: 2px;
      }
      span {
        font-size: 13px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .header-top {
    &__logo {
      width: 130px;
    }
    &__connection {
      margin-left: 25px;
    }
    &__btn {
      margin-left: 25px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .header-bottom {

    &__link a {
      font-size: 12px;
      padding: 0 7px;
    }
    &__show {
      padding: 0 7px;
      span {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header__wrapp {
    position: fixed;
    top: 0;
    right: -105%;
    width: 400px;
    height: 100vh;
    background-color: #081529;
    padding: 75px 20px 60px 20px;
    transition: all 0.5s;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 3px;
    }
    &::-webkit-scrollbar-track {
      background: #081529;
      border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
      background: #ffffff;
      border-radius: 3px;
    }
    &--active {
      right: 0;
    }
    .container {
      padding: 0;
    }
  }
  .header-top {
    padding: 0;
    &__inner {
      flex-direction: column;
      align-items: start;
      margin-top: 40px;
    }
    &__left {
      display: block;
      align-items: start;
      margin-top: 25px;
    }
    &__right {
      flex-direction: column;
      align-items: start;
      order: -1;
    }
    &__logo {
      display: none;
    }
    &__connection {
      margin: 0;
    }
    &__btn {
      position: absolute;
      top: 30px;
      left: 0;
    }
  }
  .header-bottom {
    background: transparent;
    margin-top: 40px;
    &__list {
      flex-direction: column;
      align-items: start;
      height: 100%;
      background: transparent;
      li {
        &:not(:first-child) {
          margin-top: 25px;
        }
      }
    }
    &__link a {
      align-items: start;
      color: #ffffff;
      padding: 0;
      &:after {
        display: none;
      }
      &:hover {
        background: transparent;
      }
    }
    &__item {
      width: 100%;
      &--active {
        .header-bottom__show {
          background: transparent;
        }
      }
    }
    &__show {
      justify-content: start;
      padding: 0;
      svg path {
        fill: #ffffff;
      }
      span {
        color: #ffffff;
      }
      &:after {
        display: none;
      }
      &:hover {
        background: transparent;
      }
    }
    &__hidden {
      position: static;
      min-width: 100%;
      width: calc(100% + 40px);
      margin-left: -20px;
    }
  }
  .header-mob {
    display: block;
  }
}

@media screen and (max-width: 575px) {
  .header {
    padding-left: 66px;
  }
  .header__wrapp {
    width: 100%;
  }
}

@media screen and (max-width: 399px) {
  .header {
    padding-left: 0;
  }
}