.btn-yellow {
  font-size: 14px;
  line-height: 105%;
  font-weight: 700;
  text-transform: uppercase;
  color: #081529;
  background-color: #ffba01;
  &:hover {
    background-color: #c9a028;
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.display2 {
  font-size: 40px;
  line-height: 120%;
  font-weight: 700;
  text-transform: uppercase;
  color: #081529;
  padding-left: 16px;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 6px;
    height: 100%;
    background-color: #ffba01;
  }
}
.services-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 30px;
  margin-top: 40px;
}
.services-card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 186, 1, 0.7);;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    .services-card__title {
      display: none;
    }
    .services-card__btn {
      display: block;
    }
    .services-hover {
      display: block;
    }
    &:after {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__desc {
    position: absolute;
    z-index: 2;
    left: 20px;
    bottom: 20px;
    padding-right: 20px;
  }
  &__title {
    font-size: 19px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
  &__btn {
    width: 140px;
    height: 46px;
    background-color: #081529;
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffba01;
    display: none;
  }
}
.services-hover {
  position: absolute;
  z-index: 2;
  top: 20px;
  left: 20px;
  display: none;
  &__title {
    font-size: 16px;
    line-height: 130%;
    font-weight: 700;
    text-transform: uppercase;
    color: #ffffff;
  }
}
.services-btn {
  width: 130px;
  height: 46px;
  margin: 30px auto 0;
}

@media screen and (max-width: 1200px) {
  .services-cards {
    gap: 20px;
  }
  .services-card {
    &__title {
      font-size: 16px;
    }
    &__btn {
      width: 117px;
      height: 34px;
      font-size: 12px;
    }
  }
  .services-hover {
    &__title {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 991px) {
  .display2 {
    font-size: 36px;
  }
  .services-cards {
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 30px;
  }
  .services-btn {
    margin-top: 20px;
  }
}

@media screen and (max-width: 767px) {
  .services-cards {
    grid-template-columns: 1fr 1fr;
  }
  .services-card {
    &__desc {
      left: 15px;
      bottom: 15px;
      padding-right: 15px;
    }
    &__title {
      font-size: 14px;
    }
    &__btn {
      width: 109px;
      height: 37px;
      font-size: 11px;
    }
  }
  .services-hover {
    top: 15px;
    left: 15px;
    padding-right: 15px;
    &__title {
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 575px) {
  .display2 {
    font-size: 30px;
  }
  .services-cards {
    gap: 15px;
    margin-top: 20px;
  }
  .services-card {
    &__desc {
      left: 10px;
      bottom: 10px;
    }
    &__title {
      font-size: 11px;
    }
    &__btn {
      width: 89px;
      height: 26px;
      font-size: 10px;
    }

  }
  .services-hover {
    top: 10px;
    left: 10px;
    &-title {
      font-size: 11px;
    }
  }
}